import sahayyaLogoWhite from "assets/img/common/Sahayya_Logo_White.svg";
import backIcon from "assets/img/common/backIcon.svg";
import downArrowIcon from "assets/img/common/downArrow.svg";
import searchIcon from "assets/img/common/search_icon.svg";
import NotificationIcon from "assets/img/sidebar/Notification.svg";
import HomeIcon from "assets/img/sidebar/home.svg";
import ProfileIcon from "assets/img/sidebar/profile.svg";
import IconInput from "components/IconInput";
import SideNav from "components/SideNav/SideNav";
import SimpleOverlayPanel from "components/SimpleOverlayPanel/SimpleOverlayPanel";
import SkeletonCustom from "components/Skeleton/Skeleton";
import Text from "components/Text";
import { currencyFormatter } from "locale";
import { PanelMenu } from "primereact/panelmenu";
import { useEffect, useRef, useState } from "react";
import { useSelector } from "react-redux";
import { linkAuthRoute, linkBasePath, linkPageBase, linkStaticMasterTypes } from "routes";
import AuthService from "services/AuthService";
import { formattedDateLong } from "utils/CommonUtil";
import { INPUT_STYLE_TYPE, SAHAYYA_USER_TYPE } from "utils/Constant";
import { LINK_LOAN_LIST, LINK_PARTNER_TASK_LIST } from "../transactionRoutes";

const MenuSkelton = () => {
  return <SkeletonCustom borderRadius="8px" height="24px" />;
};

function DefaultSideBar(props: any) {
  const { handleSearch, isDDEMenu, isLoading, isLoanData, isMasterData, menuItems, searchNav } =
    props;
  const overlayPanelRef = useRef(null);
  const overlayPanelMenuRef = useRef(null);
  const [profileOptions, setProfileOptions] = useState<any[]>([]);
  const loanDetail = useSelector((state: any) => state.LoanReducers.loanObj);
  // const isPartnerUser
  const userProfile = useSelector((state: any) => state.UserProfileReducers.profileObj);

  const isPartnerUser = userProfile?.sahayyaUserType === SAHAYYA_USER_TYPE.PARTNER_USER;

  useEffect(() => {
    populateOptions();
    return () => {
      console.log("DefaultSideBar Un-Mounted");
    };
  }, []);

  const populateOptions = () => {
    if (isPartnerUser) {
      setProfileOptions(PANEL_DATA.splice(2, 2));
    } else {
      setProfileOptions(PANEL_DATA);
    }
    // console.log(PANEL_DATA);
  };
  const onLoanView = (e: any) => {
    if (overlayPanelRef.current) {
      // @ts-ignore: Will fix later
      overlayPanelRef.current?.toggle(e);
    }
  };

  const renderLoanDetail = () => {
    return (
      <div className="flex flex-col gap-5 px-5">
        <div className="flex flex-row items-center justify-between px-5 py-4 -mx-5 border-b ">
          <div className="text-sm text-secondary-stext-900">File Status</div>
          <div className="flex items-center px-4 ml-2 text-sm font-bold text-center bg-purple-200 rounded-full text-secondary-stext-900 h-7">
            {loanDetail?.loanStatus}
          </div>
        </div>
        <div className="flex flex-col gap-4">
          <div className="flex flex-row justify-between">
            <div className="text-sm text-secondary-stext-900">Product</div>
            <div className="ml-2 text-sm font-bold text-secondary-stext-900">
              {loanDetail?.product}
            </div>
          </div>
          <div className="flex flex-row justify-between">
            <div className="text-sm text-secondary-stext-900">Lending type</div>
            <div className="ml-2 text-sm font-bold text-secondary-stext-900">
              {/* {loanDetail?.lanId} */}
            </div>
          </div>
          <div className="flex flex-row justify-between">
            <div className="text-sm text-secondary-stext-900">Login</div>
            <div className="ml-2 text-sm font-bold text-secondary-stext-900">
              {formattedDateLong(loanDetail?.loginDate)}
            </div>
          </div>
          <div className="flex flex-row justify-between">
            <div className="text-sm text-secondary-stext-900">Branch</div>
            <div className="ml-2 text-sm font-bold text-secondary-stext-900">
              {loanDetail?.branch}
            </div>
          </div>
          <div className="flex flex-row justify-between">
            <div className="text-sm text-secondary-stext-900">Bank/Financier</div>
            <div
              className="ml-2 text-sm font-bold truncate text-secondary-stext-900"
              title={loanDetail?.bankFinancier}
            >
              {loanDetail?.bankFinancier}
            </div>
          </div>
          <div className="flex flex-row justify-between">
            <div className="text-sm text-secondary-stext-900">Days since login</div>
            <div className="ml-2 text-sm font-bold text-secondary-stext-900">
              {loanDetail?.daysSinceLogin}
            </div>
          </div>
          <div className="flex flex-row justify-between">
            <div className="text-sm text-secondary-stext-900">Amount (₹)</div>
            <div className="ml-2 text-sm font-bold text-secondary-stext-900">
              {currencyFormatter(loanDetail?.amount)}
            </div>
          </div>
          <div className="flex flex-row justify-between">
            <div className="text-sm text-secondary-stext-900">Sales Manager</div>
            <div className="ml-2 text-sm font-bold text-secondary-stext-900">
              {loanDetail?.salesManager}
            </div>
          </div>
        </div>
      </div>
    );
  };

  const onLogout = () => {
    console.log("Logout");
    AuthService.logOut();
    window.location.href = `${window.location.origin}${linkBasePath}`;
  };

  const onProfileSetting = () => {
    console.log("Profile Setting");
    props.navigate(linkStaticMasterTypes);
  };

  const onMasterData = () => {
    const url = `${window.location.origin}${linkBasePath}${linkAuthRoute}${linkStaticMasterTypes}`;
    window.location.href = url;
  };

  const onLoanData = () => {
    const url = `${window.location.origin}${linkBasePath}${linkAuthRoute}${LINK_LOAN_LIST}`;
    window.location.href = url;
  };

  const PANEL_DATA = [
    {
      label: "label.header.panel.loanData",
      onClick: onLoanData,
    },
    {
      label: "label.header.panel.masterData",
      onClick: onMasterData,
    },
    {
      label: "label.header.panel.profileSetting",
      onClick: onProfileSetting,
    },
    {
      label: "label.header.panel.logout",
      onClick: onLogout,
    },
  ];
  // Sectional Render
  const panel = () => {
    return (
      <ul>
        {profileOptions.map((item) => {
          return (
            <li
              key={item.label}
              className="px-2 py-1 text-xs cursor-pointer text-primary-ptext-1000"
            >
              <button onClick={item.onClick}>
                <Text label={item.label} className="cursor-pointer" />
              </button>
            </li>
          );
        })}
      </ul>
    );
  };

  return (
    <div className="flex-col hidden w-56 bg-primary-sideBarBgColor md:flex">
      {isDDEMenu ? (
        <div className="flex flex-col items-start justify-start w-full gap-3 px-6 pt-6">
          <a
            href={`${
              isPartnerUser
                ? linkBasePath + linkAuthRoute + LINK_PARTNER_TASK_LIST
                : linkBasePath + linkAuthRoute + LINK_LOAN_LIST
            }`}
            className="flex flex-row cursor-pointer"
          >
            <img src={backIcon} alt="back" className="mr-1.5" />
            <Text
              label={"heading.transaction.loanListing"}
              className="text-sm text-left text-white underline cursor-pointer"
            />
          </a>
          <div className="flex flex-col w-full gap-1 text-white ">
            <button
              type="button"
              className="w-full h-8 text-base font-medium text-left truncate "
              onClick={onLoanView}
            >
              <span className="text-base">{loanDetail?.applicantName}</span>
            </button>
            <div className="h-5 text-sm">{loanDetail?.lanId}</div>
            <div className="h-4 text-xxs">
              Loan Amount (₹) : {currencyFormatter(loanDetail?.amount)}
            </div>
          </div>
          <hr className="w-full mb-4 bg-gradient-to-r from-red-100 to-red-600" />
        </div>
      ) : (
        <div className="flex flex-col items-center w-full gap-3 py-6 ">
          <img src={sahayyaLogoWhite} alt="Sahayya Logo" />

          <Text
            label={
              isMasterData ? "text.sidebar.masterData" : isLoanData ? "label.heading.loanData" : ""
            }
            className="text-xs text-white font-Roboto"
          />

          <IconInput
            variant={INPUT_STYLE_TYPE.TERTIARY}
            img={searchIcon}
            iconClassName="!w-6 !h-6 !top-0"
            placeholder="text.sidebar.search"
            value={searchNav}
            onChange={handleSearch}
            disabled={isLoading}
            hideErrorRow
          />
        </div>
      )}
      {/*Chetan: add a class and move this calc to custom css as in mobile we need to remove the calc and give 100% */}
      <div
        style={{ height: `${isDDEMenu ? "calc(100vh - 201px)" : "calc(100vh - 180px)"}` }}
        className="px-3 overflow-auto custom-scroll"
      >
        {isLoading ? (
          <div className="flex flex-col items-start justify-start h-full gap-5">
            {Array.from({ length: 10 }).map(() => {
              return <MenuSkelton key={Math.random()} />;
            })}
          </div>
        ) : (
          <>
            {isDDEMenu ? (
              <SideNav items={menuItems} loanUid={loanDetail.uid} />
            ) : (
              <PanelMenu model={menuItems} className="text-xs text-left text-white " />
            )}
          </>
        )}
      </div>

      {isDDEMenu ? (
        <div className="flex items-center justify-between w-full h-12 px-6 py-3 mt-auto text-white bg-gradient-to-t from-secondary-stext-1000 to-sideBarBgColor">
          <div>
            <button type="button" title="Home">
              <img src={HomeIcon} alt="Home" />
            </button>
          </div>
          <div className="flex gap-1">
            <button type="button">
              <img src={NotificationIcon} alt="Notification" />
            </button>

            <button
              onClick={(e) => {
                if (overlayPanelMenuRef.current) {
                  // @ts-ignore: Will fix later
                  overlayPanelMenuRef.current?.toggle(e);
                }
              }}
              className="flex flex-row items-center gap-1"
              type="button"
            >
              <img src={ProfileIcon} alt="Profile" />
              <img src={downArrowIcon} alt="Dropdown" />
            </button>

            <div></div>
          </div>
        </div>
      ) : (
        <></>
      )}

      <SimpleOverlayPanel
        className="rounded-lg shadow-lg !w-80 p-1.5 "
        ref={overlayPanelRef}
        content={renderLoanDetail()}
      />
      <SimpleOverlayPanel
        className="rounded-lg shadow-lg  !w-40 p-1.5 "
        ref={overlayPanelMenuRef}
        content={panel()}
      />
    </div>
  );
}

export default DefaultSideBar;
