import SkeletonCustom from "components/Skeleton";
import { t } from "i18next";
import { IPrimeReactMenuObj } from "interface/primereact";
import { BreadCrumb, BreadCrumbProps } from "primereact/breadcrumb";
import PropTypes from "prop-types";
import { useEffect, useState } from "react";

const propTypes = {
  items: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      command: PropTypes.func,
    })
  ),
};

interface IBreadCrumb {
  id?: string;
  class?: string;
}

interface ISimpleBreadCrumb extends BreadCrumbProps, IBreadCrumb {
  loader?: boolean;
}
function SimpleBreadCrumb(props: ISimpleBreadCrumb) {
  const [updatedModel, setUpdatedModel] = useState(props.model);

  useEffect(() => {
    let tempModel: any = props.model;
    let tempUpdatedModel: IPrimeReactMenuObj[] = [];
    tempModel?.forEach((item: any) => {
      let newObj: IPrimeReactMenuObj = {
        ...item,
        label: t(item?.label ?? ""),
      };
      tempUpdatedModel.push?.(newObj);
    });
    setUpdatedModel(tempUpdatedModel);
  }, [props.model]);

  const { home, loader } = props;
  if (loader) {
    return <SkeletonCustom height="1.25rem" width="50%" borderRadius="8px" />;
  }
  return (
    <>
      <BreadCrumb
        className="breadcrumb-wrapper text-body-copy-1"
        model={updatedModel}
        home={home}
      />
    </>
  );
}

SimpleBreadCrumb.propTypes = propTypes;

export default SimpleBreadCrumb;
