import React from "react";
import { NavLink } from "react-router-dom";
import { linkAuthRoute } from "routes";
import { LINK_LOAN_DETAIL } from "../../transactionRoutes";
import { LOCAL_STORAGE, STAGE_CODE_SCREENS } from "utils/Constant";
import { setLocalStorage } from "utils/CommonUtil";

interface ISideNavProps {
  items: any[];
  loanUid?: string;
}
function SideNav(props: ISideNavProps) {
  const { items, loanUid } = props;

  const [listItems, setListItems] = React.useState(items);

  const showPanel = (e: any, index: number) => {
    const tempItems = [...listItems];
    tempItems.forEach((item, i) => {
      if (i !== index) {
        item.showPanel = false;
      }
    });
    tempItems[index].showPanel = !tempItems[index].showPanel;
    setListItems(tempItems);
  };

  const renderItems = () => {
    const URL = `${linkAuthRoute}${LINK_LOAN_DETAIL}`;
    return listItems.map((item, index) => {
      if (item.items?.length > 0) {
        return (
          <React.Fragment key={item.uid}>
            <button
              type="button"
              className="text-white text-xs flex items-center justify-between pl-4 cursor-pointer w-full"
              onClick={(e) => showPanel(e, index)}
            >
              {item.name}
              <span
                className={`${
                  item.showPanel
                    ? " p-panelmenu-icon pi pi-chevron-down  "
                    : " p-panelmenu-icon pi pi-chevron-right "
                }   `}
              ></span>
            </button>
            {item.showPanel
              ? item.items.map((subItem: any) => {
                  let isMultipleStageExist = false;
                  STAGE_CODE_SCREENS.forEach((screen) => {
                    if (subItem.code.includes(screen)) {
                      isMultipleStageExist = true; 
                    }
                  });

                  return (
                    <NavLink
                      key={subItem.name}
                      to={
                        isMultipleStageExist
                          ? `${URL}${subItem.path}/${loanUid}/${subItem?.stage?.uid}/`
                          : `${URL}${subItem.path}/${loanUid}/`
                      }
                      onClick={() => {
                        if (isMultipleStageExist) {
                          setLocalStorage(
                            LOCAL_STORAGE.STAGE_DETAIL,
                            JSON.stringify(subItem?.stage)
                          );
                        }
                      }}
                      className={`text-white w-full text-xs pl-8 ${
                        isMultipleStageExist
                          ? window.location.pathname?.includes?.(subItem?.stage?.uid)
                            ? "bg-primary-panelBGColor"
                            : ""
                          : subItem.path && window.location.pathname?.includes?.(`${subItem.path}`)
                          ? "bg-primary-panelBGColor"
                          : ""
                      } `}
                    >
                      {subItem.name}
                    </NavLink>
                  );
                })
              : null}
          </React.Fragment>
        );
      }
      let isMultipleStageExist = false;
      STAGE_CODE_SCREENS.forEach((screen) => {
        if (item.code.includes(screen)) {
          isMultipleStageExist = true;
        }
      });
      return (
        <NavLink
          key={item.uid}
          to={
            isMultipleStageExist
              ? `${URL}${item.path}/${loanUid}/${item?.stage?.uid}/`
              : `${URL}${item.path}/${loanUid}/`
          }
          className={`text-white text-xs pl-4 ${
            item.path && window.location.pathname?.includes?.(`${item.path}`)
              ? "bg-primary-panelBGColor"
              : ""
          } `}
          onClick={(e) => {
            if (isMultipleStageExist) {
              setLocalStorage(LOCAL_STORAGE.STAGE_DETAIL, JSON.stringify(item?.stage));
            }
            showPanel(e, index);
          }}
        >
          {item.name}
        </NavLink>
      );
    });
  };
  return <div className="flex flex-col gap-4">{renderItems()}</div>;
}

export default SideNav;
