import homeIcon from "assets/img/masterdata/icons/Home.svg"; // Add title attribute to the homeIcon element
import bellIcon from "assets/img/masterdata/icons/Notifications.svg";
import bellIconWithNotification from "assets/img/masterdata/icons/NotificationsAvailableIcon.svg";
import profileIcon from "assets/img/masterdata/icons/Profile.svg";
import SimpleBreadCrumb from "components/SimpleBreadCrumb/SimpleBreadCrumb";
import SimpleOverlayPanel from "components/SimpleOverlayPanel/SimpleOverlayPanel";
import Text from "components/Text";
import React, { useEffect } from "react";
import { useSelector } from "react-redux";
import { NavLink } from "react-router-dom";
import { linkAuthRoute, linkBasePath, linkStaticMasterTypes } from "routes";
import AuthService from "services/AuthService";
import SystemDate from "services/masterdata/SystemDate";
import CommunicationService from "services/transaction/CommunicationService";
import { LINK_LOAN_LIST, LINK_NOTIFICATIONS } from "transactionRoutes";
import {
  formatDate,
  getEnvVariableValue,
  setLocalStorage,
  validateGetAPIResponse,
} from "utils/CommonUtil";
import { ENV_VAR, LOCAL_STORAGE, SAHAYYA_USER_TYPE } from "utils/Constant";

function DefaultHeader(props: any) {
  const [currentDate, setCurrentDate] = React.useState("");
  const [menuItems, setMenuItems] = React.useState<any[]>([]);
  const [isNotificationAvailable, setIsNotificationAvailable] = React.useState<boolean>(false);
  // @ts-ignore: Will fix later
  const userProfile = useSelector((state: any) => state.UserProfileReducers.profileObj);

  const overlayPanelRef = React.useRef(null);
  /*
    Hooks
  */
  useEffect(() => {
    const intervalId = setInterval(
      getNotifications,
      getEnvVariableValue(ENV_VAR.VITE_NOTIFICATION_POLLING)
    );
    getCurrentDate();
    populateMenu();
    getNotifications();
    console.log("DefaultHeader Mounted");
    return () => {
      console.log("DefaultHeader Un-Mounted");
      clearInterval(intervalId);
    };
  }, []);

  const getCurrentDate = async () => {
    try {
      const res = await SystemDate.getCurrentDate();
      setCurrentDate(formatDate(res.data?.date));
      setLocalStorage(LOCAL_STORAGE.SYSTEM_DATE, res.data?.date);
    } catch (error) {
      console.log(error);
    }
  };

  const populateMenu = () => {
    if (userProfile?.sahayyaUserType === SAHAYYA_USER_TYPE.PARTNER_USER) {
      console.log("PARTNER_USER");
      setMenuItems(PANEL_DATA.splice(2, 2));
    } else {
      console.log("USER");
      setMenuItems(PANEL_DATA);
    }
  };

  const getNotifications = async () => {
    const search = `&isViewed=false&userUid=${userProfile?.userUid}&notificationType=web`;
    try {
      const res = await CommunicationService.getAllNotifications(0, 1, search);
      if (validateGetAPIResponse(res)) {
        if (res?.data?.results?.length >= 1) {
          setIsNotificationAvailable(true);

          if (window.location.pathname.includes(LINK_NOTIFICATIONS)) {
            try {
              const res = await CommunicationService.updateNotificationStatusToRead();
              setIsNotificationAvailable(false);
            } catch (err) {
              console.log(err);
            }
          }
        } else {
          setIsNotificationAvailable(false);
        }
      }
    } catch (err) {
      console.log(err);
    }
  };

  // On Click Event
  const onLogout = () => {
    console.log("Logout");
    AuthService.logOut();
    window.location.href = `${window.location.origin}${linkBasePath}`;
  };

  const onProfileSetting = () => {
    console.log("Profile Setting");
    props.navigate(linkStaticMasterTypes);
  };

  const onMasterData = () => {
    const url = `${window.location.origin}${linkBasePath}${linkAuthRoute}${linkStaticMasterTypes}`;
    window.location.href = url;
  };

  const onLoanData = () => {
    const url = `${window.location.origin}${linkBasePath}${linkAuthRoute}${LINK_LOAN_LIST}`;
    window.location.href = url;
  };

  const PANEL_DATA = [
    {
      label: "label.header.panel.loanData",
      onClick: onLoanData,
    },
    {
      label: "label.header.panel.masterData",
      onClick: onMasterData,
    },
    {
      label: "label.header.panel.profileSetting",
      onClick: onProfileSetting,
    },
    {
      label: "label.header.panel.logout",
      onClick: onLogout,
    },
  ];

  // Sectional Render
  const panel = () => {
    return (
      <ul>
        {menuItems.map((item) => {
          return (
            <li
              key={item.label}
              className="px-2 py-1 text-xs text-primary-ptext-1000 cursor-pointer"
            >
              <button onClick={item.onClick}>
                <Text label={item.label} className="cursor-pointer" />
              </button>
            </li>
          );
        })}
      </ul>
    );
  };

  return (
    <div className="flex flex-col pb-3 gap-3 ">
      <div className="w-full flex flex-row items-center justify-between h-9">
        <button type="button" title="home-icon">
          <img src={homeIcon} alt="Home Icon" title="home icon" />
        </button>
        <div className="flex items-center">
          <span className="text-xs text-gray-500 mr-4 ">System Date : {currentDate}</span>
          <NavLink to={linkAuthRoute + LINK_NOTIFICATIONS} title="bell icon">
            <img
              src={isNotificationAvailable ? bellIconWithNotification : bellIcon}
              alt="bell Icon"
              className="mr-6"
            />
          </NavLink>
          <button
            type="button" // Add type attribute and set it to "button"
            onClick={(e) => {
              if (overlayPanelRef.current) {
                // @ts-ignore: Will fix later
                overlayPanelRef.current?.toggle(e);
              }
            }}
            title="profile icon"
          >
            <img src={profileIcon} alt="Home Icon" className="cursor-pointer" />
          </button>
        </div>
      </div>
      <SimpleBreadCrumb model={props.breadCrumbList} />
      <SimpleOverlayPanel
        className="rounded-lg shadow-lg  !w-40 p-1.5 "
        ref={overlayPanelRef}
        content={panel()}
      />
    </div>
  );
}

export default DefaultHeader;
