import ButtonBox from "components/ButtonBox";
import Text from "components/Text";
import { RouteProps, withRouter } from "hoc/withRouter";
import { IRouteObj } from "interface/common";
import queryString from "query-string";
import { Suspense, useEffect, useState } from "react";
import { Outlet, Route, Routes, useParams } from "react-router-dom";
import routes, { linkAuthRoute, linkMasterDataBase } from "routes";
import { getMenu } from "services/staticService/MenuService";
import { BUTTON_TYPE, LOCAL_STORAGE, SIDE_MENU_ROOT_CODES } from "utils/Constant";
import Page404 from "views/Page404/Page404";
import AuthValidator from "../components/AuthValidator";
import Loader from "../components/Loader";
import {
  LINK_CAM,
  LINK_DDE_CREDIT_QUERY,
  LINK_DDE_LOGIN,
  LINK_DDE_QC,
  LINK_DEDUPE,
  LINK_FINAL_APPROVAL,
  LINK_FINANCIAL_APPROVAL,
  LINK_IN_PRINCIPLE_APPROVAL,
  LINK_LEGAL,
  LINK_LOAN_DATA,
  LINK_LOAN_DETAIL,
  LINK_PD,
  LINK_RCU,
  LINK_RE_APPEAL,
  LINK_TECHNICAL,
} from "../transactionRoutes";
import DefaultHeader from "./DefaultHeader";
import DefaultSideBar from "./DefaultSideBar";
import UserService from "services/masterdata/User/UserService";
import { useDispatch } from "react-redux";
import { saveUserProfile } from "actions/UserProfileAction";
import StaticMasterDataService from "services/masterdata/StaticMaster/StaticMasterDataService";
import { setLocalStorage, validateGetAPIResponse } from "utils/CommonUtil";

function DefaultLayout(props: RouteProps) {
  const [isLoadingMenu, setIsLoadingMenu] = useState(true);
  const [menuItem, setMenuItem] = useState<any>([]);
  const [originalMenuItem, setOriginalMenuItem] = useState<any>([]);
  const [searchNav, setSearchNav] = useState("");
  const [breadCrumbList, setBreadCrumbList] = useState<any>([]);
  const [reviewMode, setReviewMode] = useState<string | undefined>(undefined);
  const [id, setId] = useState<string | undefined>(undefined);
  const [isMasterData, setMasterData] = useState(false);
  const [isDDERoute, setIsDDERoute] = useState(false);
  const [isLoanData, setIsLoanData] = useState(false);
  const [onMenuClick, setOnMenuClick] = useState(false);

  const params = useParams();
  const dispatch = useDispatch();

  useEffect(() => {
    init();
    getAllStaticMaster();
  }, []);

  useEffect(() => {
    if (menuItem.length > 0) {
      prepareBreadCrumbList();
    }
  }, [menuItem]);

  //  Maintained this useEffect to don't re render the menu items
  useEffect(() => {
    if (menuItem.length > 0) {
      const mItems = [...originalMenuItem];
      mItems.forEach((menu: any) => {
        if (menu.items && menu.items.length > 0) {
          menu.items = menu.items.map((subMenu: any) => {
            const splitPath = props.location.pathname.split(linkAuthRoute);
            let icon = "";
            if (splitPath.length > 1) {
              if (splitPath[1] === subMenu.path) {
                icon = "pi pi-fw pi-circle ml-3 mr-3 ";
              }
            }
            return {
              ...subMenu,
              icon,
              className: !icon ? "text-xs flex ml-10" : "",
              command: () => {
                setOnMenuClick(!onMenuClick);
                props.navigate(subMenu.path);
              },
            };
          });
        }
      });
      setMenuItem(mItems);
    }
  }, [onMenuClick]);

  useEffect(() => {
    setRoute();
    const parsed: {
      mode?: string;
      reviewMode?: string;
      id?: string;
    } = queryString.parse(props.location.search);

    if (parsed?.reviewMode && parsed?.reviewMode !== undefined) {
      setReviewMode(parsed.reviewMode);
    }
    if (parsed?.id && parsed?.id !== undefined) {
      setId(parsed.id);
    }
  }, [window.location.pathname]);

  useEffect(() => {
    //This code is to filter the sidebar items based on user input:
    const filterMenuItems = (menuItems: any, searchNav: any) => {
      return menuItems.filter((item: any) => {
        if (item.label.toLowerCase().includes(searchNav.toLowerCase())) {
          return true; // Include if searchNav is found in the current item's label
        } else if (item.items) {
          // Recursively search in nested items
          const filteredItems = filterMenuItems(item.items, searchNav);
          return filteredItems.length > 0; // Include if any nested items match the searchNav
        }
        return false; // Exclude if neither the current item nor any nested items match the searchNav
      });
    };

    if (searchNav.toLowerCase() === "") {
      setMenuItem(originalMenuItem ?? []);
    } else {
      const filteredMenuItems = filterMenuItems(originalMenuItem, searchNav);
      setMenuItem(filteredMenuItems);
    }
  }, [searchNav]);

  const init = async () => {
    setRoute();
    getUserDetails();
    await fetchMenuItems();
  };

  const getUserDetails = async () => {
    UserService.getUserDetail().then((res) => {
      console.log("res", res);
      dispatch(saveUserProfile(res.data));
    });
    // const userDetails = await getUser();
    // setUser(userDetails);
  };

  const fetchMenuItems = async () => {
    setIsLoadingMenu(true);

    let code = SIDE_MENU_ROOT_CODES.MASTER_DATA;
    if (props.location.pathname.includes(linkMasterDataBase)) {
      code = SIDE_MENU_ROOT_CODES.MASTER_DATA;
    } else if (props.location.pathname.includes(LINK_LOAN_DATA)) {
      if (props.location.pathname.includes(LINK_LOAN_DETAIL)) {
        code = SIDE_MENU_ROOT_CODES.LOAN_APPLICATION_MENU;
      } else {
        code = SIDE_MENU_ROOT_CODES.LOAN_LISTING;
      }
    }
    let loanUid = "";
    if (code === SIDE_MENU_ROOT_CODES.LOAN_APPLICATION_MENU && params) {
      const stageUidPaths = [
        LINK_DDE_LOGIN,
        LINK_LEGAL,
        LINK_PD,
        LINK_TECHNICAL,
        LINK_RCU,
        LINK_FINAL_APPROVAL,
        LINK_IN_PRINCIPLE_APPROVAL,
        LINK_FINANCIAL_APPROVAL,
        LINK_DEDUPE,
        LINK_CAM,
        LINK_DDE_CREDIT_QUERY,
        LINK_RE_APPEAL,
        LINK_DDE_QC
      ];
      const doesExist = stageUidPaths.some((route) => window.location.pathname.includes(route));

      console.log("Does exist:===", doesExist);
      const splitPath = params["*"]?.split("/");
      const loanUidIndex = doesExist ? 3 : 2;
      console.log("splitPath====", splitPath);

      if (splitPath) {
        loanUid = splitPath[splitPath.length - loanUidIndex];
      }
    }
    const mItems = await getMenu(code, loanUid);

    mItems.forEach((menu: any) => {
      menu.icon = <img src={menu.icon} className="mr-3" alt={menu.label} />;
      if (menu.items && menu.items.length > 0) {
        menu.items = menu.items.map((subMenu: any) => {
          const splitPath = props.location.pathname.split(linkAuthRoute);
          let icon = "";
          if (splitPath.length > 1) {
            if (splitPath[1] === subMenu.path) {
              icon = "pi pi-fw pi-circle ml-3 mr-3 ";
            }
          }
          return {
            ...subMenu,
            icon,
            className: !icon ? "text-xs flex ml-10" : "",
            command: () => {
              setOnMenuClick(!onMenuClick);
              props.navigate(subMenu.path);
            },
          };
        });
      }
    });
    console.log("mItem", mItems);
    setIsLoadingMenu(false);
    setMenuItem(mItems);
    setOriginalMenuItem(mItems);
    // This code is to set the page mode
  };

  // Setting the module based on url
  const setRoute = () => {
    const hideHeaderPathList = [LINK_LOAN_DETAIL];

    if (hideHeaderPathList.some((path) => props.location.pathname.includes(path))) {
      setIsDDERoute(true);
    } else {
      setIsDDERoute(false);
    }
    if (props.location.pathname.includes(LINK_LOAN_DATA)) {
      setIsLoanData(true);
    } else {
      setIsLoanData(false);
    }
    if (props.location.pathname.includes(linkMasterDataBase)) {
      setMasterData(true);
    } else {
      setMasterData(false);
    }
  };

  const handleSearch = (event: any) => {
    const { value } = event.target;
    setSearchNav(value);
  };

  const prepareBreadCrumbList = () => {
    const splitPath = props.location.pathname.split(linkAuthRoute);
    setBreadCrumbList([]);
    console.log("BREAD CRUMB");
  };

  const getAllStaticMaster = async () => {
    try {
      const res = await StaticMasterDataService.getAllCode();
      if (validateGetAPIResponse(res)) {
        const staticMasterMap: any = {};
        res.data.forEach((element: any) => {
          staticMasterMap[element.code] = element.name;
        });
        setLocalStorage(LOCAL_STORAGE.STATIC_MASTER, JSON.stringify(staticMasterMap));
      }
    } catch (error) {
      console.log(error);
    }
  };

  // console.log("breadCrumbList===", breadCrumbList);

  return (
    <div className="w-full flex overflow-hidden bg-primary-sapphireBlue-100">
      <DefaultSideBar
        handleSearch={handleSearch}
        isDDEMenu={isDDERoute}
        isLoading={isLoadingMenu}
        isLoanData={isLoanData}
        isMasterData={isMasterData}
        menuItems={menuItem}
        searchNav={searchNav}
        {...props}
      />
      <div className=" bg-primary-sapphireBlue-100 max-w-328 m-auto flex-1 h-screen overflow-hidden p-3">
        {isDDERoute ? <></> : <DefaultHeader breadCrumbList={breadCrumbList} {...props} />}
        {reviewMode && (
          <div className="h-20 bg-semantic-deepSkyBlue-100 rounded-masterDataPageRadius flex items-center justify-between">
            <div className="ml-5">
              <Text
                label="header.label.reviewMode"
                args={{
                  reviewMode: reviewMode.toLowerCase().replace(/(?:^|\s)\w/g, function (match) {
                    return match.toUpperCase();
                  }),
                  id,
                }}
                className="text-xs font-bold"
              />
            </div>
            <div className="mr-5">
              <ButtonBox buttonType={BUTTON_TYPE.CLOSE_OUTLINE} onClick={() => window.close()} />
            </div>
          </div>
        )}
        <main
          className={`${
            isLoanData
              ? "   overflow-auto "
              : " p-3 rounded-masterDataPageRadius bg-white overflow-scroll screen-body-scroll "
          }`}
        >
          {/* <main className={`${isLoanData} rounded-masterDataPageRadius`}> */}
          <Suspense fallback={<Loader />}>
            <Routes>
              {routes.map((route: IRouteObj, idx: number) => {
                return route.component ? (
                  <Route
                    key={idx}
                    path={route.path}
                    element={
                      <AuthValidator
                        isMenuLoading={isLoadingMenu}
                        AuthorizedComponent={route.component}
                        urlPath={route.path}
                      />
                    }
                  />
                ) : null;
              })}
              <Route path="*" element={<Page404 />} />
            </Routes>
          </Suspense>
          <Outlet />
        </main>
      </div>
    </div>
  );
}

export default withRouter(DefaultLayout, true);
